.header{
  width: 100%;
  height: 100vh;
  font-weight: 300;
  
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 5%;
}

.title{
  font-size: calc(40px + 2vmin);
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loader:empty {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  width: 6em;
  height: 6em;
  border: 0.5em solid rgba(219, 34, 34, 0.2); 
  border-left: 0.5em solid #845ec2;
  border-radius: 50%;
  animation: load8 1.1s infinite linear;
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/*** NAVIGATION BAR ***/

.navBar {
  z-index: 2;
  top: 0;
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 6vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: 0.7s;
  /*-webkit-box-shadow: 0px 89px 111px -20px rgba(0,0,0,0.6);
  -moz-box-shadow: 0px 89px 111px -20px rgba(0,0,0,0.6);
  box-shadow: 0px 89px 111px -20px rgba(0,0,0,0.6);*/
}

.logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
}

.navBar a {
  color: white;
  text-decoration: none;
  padding-right: 20px;
}

.navBar.scrolled {
  background-color: rgba(63, 177, 241, 1) !important;
  /* color: rgba(63, 177, 241, 1); */
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.4);
  transition: 0.6s linear;
}
